.cat-img {
  object-fit: cover;
  margin-top: 100px;
}

.loader-container {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.loader {
  border: 3px solid #fff;
  animation: rotation 1s linear infinite;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 3px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  display: none;
}

.description {
  width: 500px;
}

.text-decotarion {
  font-size: medium;
  font-weight: 600;
}

